<template>
  <div :class="$style.wrapper">
    <t-table :columns="headers" :data="tableData"></t-table>
  </div>
</template>

<script>
import tTable from '@/components/utils/table/table'
export default {
  components: {
    tTable,
  },
  data() {
    return {
      headers: [
        {label: '', prop: 'parameter'},
        {label: '', prop: 'description'},
        {label: '', prop: 'type'},
        {label: '', prop: 'default'},
        {label: '', prop: 'example'},
      ],
      tableData: [
        {
          parameter: 'columns',
          description: '(label)(prop)',
          type: 'array',
          default: '[ ]',
          example: '[{ label: example, prop: example_prop }]',
        },
        {
          parameter: 'data',
          description: '',
          type: 'array',
          default: '[ ]',
          example: '[{ prop1: data1, prop2: data2, prop3: data3 }]',
        },
        {
          parameter: 'bordered',
          description: '',
          type: 'boolean',
          default: 'True',
          example: 'True',
        },
        {
          parameter: 'borderAround',
          description: '',
          type: 'boolean',
          default: 'True',
          example: 'True',
        },
        {
          parameter: 'borderColor',
          description: '',
          type: 'string',
          default: '#d3d3d3',
          example: '#000',
        },
        {
          parameter: 'striped',
          description: '',
          type: 'boolean',
          default: 'False',
          example: 'True',
        },
        {
          parameter: 'rowHover',
          description: 'mouse over hover',
          type: 'boolean',
          default: 'True',
          example: 'True',
        },
        {
          parameter: 'hoverStyle',
          description: 'mouse over hover style',
          type: 'string',
          default: 'light',
          example: 'hoverStyle="dark", hoverStyle="light"',
        },
        {
          parameter: 'headerFontSize',
          description: '',
          type: 'string',
          default: '',
          example: '14px',
        },
        {
          parameter: 'headerColor',
          description: '',
          type: 'string',
          default: '#3F3F3F',
          example: '#000',
        },
        {
          parameter: 'headerTextAlign',
          description: '',
          type: 'string',
          default: 'default',
          example: 'center',
        },
        {
          parameter: 'headerPadding',
          description: 'padding',
          type: 'string',
          default: '8px',
          example: '1px',
        },
        {
          parameter: 'headerBackgroundColor',
          description: '',
          type: 'string',
          default: '#ECECEC',
          example: '#fff',
        },
        {
          parameter: 'backgroundColor',
          description: '',
          type: 'string',
          default: '',
          example: '#fff',
        },
        {
          parameter: 'spanMethod',
          description: '',
          type: 'function',
          default: 'function(){return {rowspan: 1, colspan: 1} }',
          example: '',
        },
        {
          parameter: 'spanMethod_header',
          description: '',
          type: 'function',
          default: 'null',
          example: '',
        },
        {
          parameter: 'tbodyGroup',
          description: 'table body',
          type: 'object',
          default: 'null',
          example: '{groupNumber: 2, groupSize: [10, 5]}, groupNumber->groupSize->',
        },
        {
          parameter: 'rowToggled',
          description: '',
          type: 'boolean',
          default: 'False',
          example: 'True',
        },
        {
          parameter: 'height',
          description: ', overflow-y:srcoll',
          type: 'string',
          default: '',
          example: '500px',
        },
        {
          parameter: 'maxHeight',
          description: ', overflow-y:srcoll',
          type: 'string',
          default: '',
          example: '500px',
        },
        {
          parameter: 'clickEvent',
          description: 'row, row_indexrow',
          type: 'function',
          default: 'null',
          example: 'function evt(row) { console.log(data[row]) }',
        },
        {
          parameter: 'loading',
          description: 'loadingloadingslot',
          type: 'boolean',
          default: 'False',
          example: 'True',
        },
      ]
    }
  },
}
</script>

<style lang="scss" module>
@import '@/styles/general.scss';
.wrapper {
  @include block();
}
</style>